@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  background-attachment: scroll;
  font-family: "Roboto", sans-serif;
}

.content {
  font-size: calc(20px + 2vmin);
  color: white;
}

.title {
  z-index: 1;
  padding-top: 5vh;
  padding-left: 5vw;
  height: 100vh;
}

.logo {
  z-index: 1;
}

.ripple {
  position: absolute;
  top: 50vh;
  left: 50vw;
  background-color: rgba(255, 255, 255, 0.1);
  width: 0.1rem;
  height: 0.1rem;
  border-radius: 50%;
  -webkit-animation: background-move infinite 6s linear;
          animation: background-move infinite 6s linear;
}

.content-logo {
  position: absolute;
  top: calc(50vh - 100px);
  left: calc(50vw - 100px);
  width: 200px;
  height: 200px;
  pointer-events: none;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.cert-container {
  z-index: 2;
  padding-top: 5vh;
  padding-left: 5vw;
  overflow: auto;
  height: 100vh;
}

.cert-sub-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.cert-container .showBtn {
  background-color: rgba(95, 95, 95, 0.3);
  font-size: 50%;
  margin-top: 20px;
  text-align: center;
  width: 8vw;
  padding: 10px;
  border-radius: 25px;
  transition: 0.25s;
}

.cert-container .showBtn:hover {
  cursor: pointer;
  background-color: rgba(95, 95, 95, 0.5);
  font-size: 55%;
  transition: 0.25s;
}

.cert-container .showBtn:active {
  cursor: pointer;
  background-color: rgba(95, 95, 95, 1);
  font-size: 55%;
  transition: 0.25s;
}

.vertical-buttons {
  width: 10vw;
  position: -webkit-sticky;
  position: sticky;
  left: 85vw;
  bottom: 5vh;
}

.vertical-buttons ul {
  padding: 0;
  margin: 0;
  width: 10vw;
  list-style: none;
}

.vertical-buttons li {
  text-align: center;
  color: white;
  padding: 10px;
  margin: 10px;
  background-color: rgba(95, 95, 95, 0.3);
  border-radius: 25px;
  transition: 0.25s;
}

.vertical-buttons li:hover {
  cursor: pointer;
  background-color: rgba(95, 95, 95, 0.5);
  font-size: 105%;
  transition: 0.25s;
}

.vertical-buttons li:active {
  cursor: pointer;
  background-color: rgba(95, 95, 95, 1);
  font-size: 105%;
  transition: 0.25s;
}

.contact-container {
  height: 100vh;
  padding-top: 5vh;
  padding-left: 5vw;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes background-move {
  0% {
    box-shadow: 0 0 0 0rem rgba(255, 255, 255, 0.1);
  }
  100% {
    box-shadow: 0 0 0 60rem rgba(255, 255, 255, 0);
  }
}

@keyframes background-move {
  0% {
    box-shadow: 0 0 0 0rem rgba(255, 255, 255, 0.1);
  }
  100% {
    box-shadow: 0 0 0 60rem rgba(255, 255, 255, 0);
  }
}

@media (max-width: 768px) {
}

.cert-card {
  display: inline-block;
  text-align: center;
  box-shadow: 5px 5px 3px rgba(101, 186, 207, 0.5);
  background-color: rgba(201, 192, 192, 0.2);
  cursor: pointer;
  border-radius: 3%;
  margin-right: 20px;
  margin-top: 20px;
  font-size: 15px;
}

.cert-title {
  font-size: 20px;
  padding: 10px 0;
}

.cert-body img {
  width: 20vw;
}

